.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #eef5f8;
}

.containerBlock {
    background-color: #accede;
    padding: 40px;
    border-radius: 10px;
    color: #445258;
}

.header {
    text-align: center;
    font-size: 26px;
    padding-bottom: 15px;
}

.buttonBlock {
    padding-top: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 10px;

}

.display {
    text-align: center;
    font-size: 26px;
    /*padding-bottom: 30px;*/
    border: 3px solid #78909b;
    border-radius: 5px;
    align-items: center;
    padding: 15px;
}

.button {
    padding: 8px;
    font-size: 20px;
    border-radius: 5px;
    color: #22292c;
    background-color: #89a4b1;
    border: none;
}

